}

.slider{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    #swiperbox{
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100vh;
        .wrap {
            width: 100%;
            height: 100%;
            list-style: none;
            padding: 0;
            margin: 0;
          }
          li {
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            opacity: 0;
            transition: opacity 0.5s ease-in-out;
            img{
                height: 100vh;
            }
          }
          li.active {
            opacity: 1;
          }
    }
    
}
.d-none{
    display: none;
}

