.wrap{
    width: 100vw;
    height: 100vh;
    margin: 0 auto;
    .popup.active{
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        width: 600px;
        height: 100px;
    }
    .popup{
        // display: none;
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        text-align: center;
        position: absolute;
        top: 60px;
        left: 20px;
        width: 600px;
        overflow: hidden;
        height: 0px;
        transition: 0.3s;
        background-color: rgba(0, 0, 0, 0.719);
        .close{
            font-size: 14pt;
            position: absolute;
            right: 10px;
            top: 10px;
            background-color: transparent !important;
            color: white;
            border: none;
            cursor: pointer;
        }
        li{
            margin-top: 5px;
            margin-bottom: 5px;
        }
        z-index: 9999999999;
        color: white;
    }
    .section{
        .by{
            z-index: 9999;
            display: flex;
            position: absolute;
            margin-left: 20px;
            opacity: 0;
            animation: fadedown 0.3s ease-in 0.2s forwards;
            @keyframes fadedown {
                0%{
                    opacity: 0;
                    margin-top: 0;
                }
                100%{
                    opacity: 1;
                    margin-top: 10px;
                }
            }
            color: white;
            button{
                cursor: pointer;
                background-color: transparent;
                border: none;
                margin-left: 6px;
                font-size: 14pt;
                color: white;
                position: relative;
                z-index: 9999;
            }
        }
        position: relative;
        width: 100%;
        height: 100%;
        background-image: url(../asset/bg.png);
        background-repeat: no-repeat;
        background-size: cover;
        overflow: hidden;
        .underbg{
            width: 100%;
            height: 100%;
            background-image: url(../asset/under_bg.jpg);
            position: absolute;
            z-index: -100;
        }

