        .vs_g{
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -45%);
            width: 230px;
            height: 230px;
            background-size: contain;
            background-repeat: no-repeat;
            z-index: 9999;
            opacity: 0;
            animation: scaleIn 0.6s ease-in-out 0.4s forwards;
            mix-blend-mode:screen;
    
            .img{
                background-image: url('../asset/vs.png');
                height: 100%;
                background-repeat: no-repeat;}
                
            @keyframes scaleIn {
                0% {
                    opacity: 0;
                    transform: translate(-50%, -45%) scale(0.5);
                }
                50% {
                    opacity: 1;
                    transform: translate(-50%, -45%) scale(1.1); /* Slightly larger for bounce effect */
                }
                70% {
                    transform: translate(-50%, -45%) scale(0.95); /* Slightly smaller for bounce effect */
                }
                100% {
                    opacity: 1;
                    transform: translate(-50%, -45%) scale(1);
                }
            }
        }
        
