                .frame{
                    filter: brightness(0.6);
                    width: 100%;
                    height: 100%;
                    
                    iframe{
                        width: 280px;
                        height: 280px;
                        position: absolute;
                        top: 0;
                        left: 0;
                        z-index: -1;
                    }
                }
                li:nth-child(2){
                    font-family: 'DOSIyagiMedium' !important;
                    font-size: 14pt;
                    display: flex;
                    p{
                        height: 30px;
                        line-height: 30px;
                        margin: 0;
                        margin-right: 7px;
                    }
                    .equalizer {
                        font-size: 0;
                        display: flex;
                        align-items: center;
                        i {
                          background-color: #fff;
                          content: '';
                          height: 15px;
                          
                          width: 3px;
                          margin: 0 3px;
                          animation: move-up-and-down .3s infinite alternate;
                          transform-origin: bottom left;
                        }
                      }
                      
                      $delay: (.3s, .1s, .2s, .1s, .2s, .2s, .1s, .2s, .2s, .1s, .3s, .2s);
                      $bgcolors: (#9400d3, #4b0082, #0000ff, #00ff00, #ffff00, #ff7f00, #ff0000, #ff7f00, #ffff00, #00ff00, #0000ff, #4b0082, #9400d3);
                      
                      @for $i from 1 through length($delay) {
                        i:nth-of-type(#{$i}) {
                          animation-delay: nth($delay, $i);
                          // background-color: nth($bgcolors, $i);
                        }
                      }
                      
                      @keyframes move-up-and-down {
                        0% {
                          opacity: .3;
                          transform: scaleY(.05);
                        }
                        100% {
                          opacity: 1;
                          transform: scaleY(1);
                        }
                      }
                }
                li:nth-child(3){
                    margin-top: -2px;
                    font-size: 13pt;
                    margin-bottom:12px;
                    letter-spacing: -1px;
                }
                li{
                    position: relative;
                    z-index: 9999;
                    font-family: 'PFStardust' !important;
                    font-size: 12pt;
                    height: 30px;
                    line-height: 30px;
                    text-shadow: 1px 1px 12px black;
                }
            }
            iframe:after{
                content: "";
                width: 100%;
                height: 100%;
                background: linear-gradient(180deg, transparent, rgba(0, 0, 0, 0.8));
                position: absolute;
                top: 0;
                left:0;
            }
        }
        }
       
    .selectbox{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, 50%);
        display: flex;
        flex-direction: column;
        align-items: center;
        z-index: 9999;
        opacity: 0;
        animation: underfade 0.8s ease-in-out 0.8s forwards;
        @keyframes underfade {
            0% {margin-top: -20px; opacity: 0;} /* 처음 위치 */
            100% {margin-top: 10px; opacity: 1;} /* 마지막 위치 */
          }
        .info{
            margin-bottom: 18px;
            font-size: 35pt;
            color: white;
            font-family: 'DOSIyagiMedium' !important;
        }
        .first{
            margin-bottom: 15px;
        }
        .first, .second{
            display: flex;
        }
        .reset{
            position: relative;
            z-index: 99999;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 200px;
            height: 60px;
            border-radius: 60px;
            background-color: transparent;
            border: 2px solid rgb(0, 0, 0);
            cursor: pointer;
            color: black;
            // font-weight: bold;
            font-size: 18pt;
            transition: 0.3s;
            font-family: "DOSIyagiMedium" !important;
            margin-top: 50px;
            i{
                font-size: 20pt;
            }
        }
  
        .reset:hover{
            background-color: black;
            color:white;
            transform: scale(1.1);
        }
        .c1p{
            border: 2px solid red;
        }
        .c2p{
            order: 2px solid blue;
        }
        .first > button, .second > button{
            position: relative;
            margin-right: 15px;
            width: 76px;
            height: 76px;
            border-radius: 0px 20px 0px 20px;
            border: none;
            box-sizing: border-box;
            overflow: hidden;
            cursor: pointer;
            transition: 0.3s;
            background: #A9A9A9;
            img{
                box-sizing: border-box;
                position: absolute;
                width: 100%;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);         
                transition: 0.3s;
            }
        }
        button.clicked1p{
            border: 4px solid red !important;
        }
        button.clicked2p{
            border: 4px solid blue !important;
        }
        .first > button img, .second > button img{
            filter: brightness(0);
        }
        .first > button:hover img{
            filter:brightness(100%);
            transform: translate(-50%, -50%)
        }
        .second > button:hover img{
            filter:brightness(100%);
            transform: translate(-50%, -50%)
        }

    }
    }   


}

